<template>
  <div class="admin-user">
    <div class="edit-user">
      <el-button @click="ui.formVisible = true">/</el-button>
      <!-- <div class="search f-right">
        <form @submit.prevent="search">
          <el-input placeholder="" prefix-icon="el-icon-search" v-model="keyword" clearable></el-input>
          <button type="submit" hidden></button>
        </form>
      </div> -->
    </div>
    <div class="form-edit" v-if="ui.formVisible">
      <form @submit.prevent="createOrUpdate">
        <div class="input-field">
          <el-input type="text" placeholder="" v-model="form.nickname" />
          <div class="error-list">
            <div class="error" v-for="(invalid, e) in errors.nickname" :key="e.id">
              <span v-if="invalid">{{rules.nickname[e].msg}}</span>
            </div>
          </div>
        </div>
        <div class="input-field">
          <el-input type="text" placeholder="" v-model="form.phone"/>
          <div class="error-list">
            <div class="error" v-for="(invalid, e) in errors.phone" :key="e.id">
              <span v-if="invalid">{{rules.phone[e].msg}}</span>
            </div>
          </div>
        </div>
        <div class="input-field">
          <el-input type="text" placeholder="" v-model="form.mail"/>
          <div class="error-list">
            <div class="error" v-for="(invalid, e) in errors.mail" :key="e.id">
              <span v-if="invalid">{{rules.mail[e].msg}}</span>
            </div>
          </div>
        </div>
        <div class="input-field">
          <el-input type="text" placeholder="" v-model="form.password" />
          <div class="error-list">
            <div class="error" v-for="(invalid, e) in errors.password" :key="e.id">
              <span v-if="invalid">{{rules.password[e].msg}}</span>
            </div>
          </div>
        </div>
        <div class="input-field">
          <el-button native-type="submit"></el-button>
        </div>
      </form>
    </div>
    <div class="table-user">
      <el-table :data="list" style="width: 100%" max-height="500">
        <el-table-column fixed prop="nickname" label="" width="80"></el-table-column>
        <el-table-column prop="phone" label="" width="120"></el-table-column>
        <el-table-column prop="mail" label="" width="120"></el-table-column>
        <el-table-column prop="password" label="" min-width="120"></el-table-column>
        <el-table-column fixed="right" label="" width="160">
          <template slot-scope="scope">
            <el-button size="mini" @click="edit(scope.$index, scope.row)"></el-button>
            <el-button size="mini" type="danger" @click="remove(scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination text-center">
        <el-pagination
          layout="prev, pager, next"
          :total="total"
          :page-size="readParam.limit"
          :current-page="readParam.page"
          @current-change="flip"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import admin from "../../mixin/admin";
export default {
  mixins: [admin],
  data() {
    return {
      model: "user",
      rules: {
        nickname: {
          unique: {
            params: ["user", "exists", "nickname"],
            msg: ""
          },
          required: {
            msg: ""
          },
          lengthBetween: {
            params: [2, 12],
            msg: "212"
          },
          regex: {
            params: [/^[a-zA-Z0-9\u4e00-\u9fa5]{2,10}$/],
            msg: ""
          }
        },
        phone: {
          unique: {
            params: ["user", "exists", "phone"],
            msg: ""
          },
          phone: {
            msg: ""
          }
        },
        mail: {
          unique: {
            params: ["user", "exists", "mail"],
            msg: ""
          },
          mail: {
            msg: ""
          }
        },
        password: {
          required: {
            msg: ""
          },
          lengthBetween: {
            params: [6, 24],
            msg: "624"
          },
          regex: {
            params: [/(?=[^0-9]*[0-9]+)(?=[^a-zA-Z]*[a-zA-Z]+)/],
            msg: ""
          }
        }
      },
      keyword: ""
    };
  },
  methods: {}
};
</script>

<style scoped>
.admin-user {
  padding: 1em;
}

.edit-user,
.cancel {
  margin-bottom: 20px;
}

.edit-user button {
  margin-bottom: 1em;
}

.table-user {
  border: 1px solid #ccc;
  padding: 5px;
  background: #fff;
  box-shadow: 0 1px 2px rgba(46, 61, 73, 0.08);
}

.input-field {
  margin-bottom: .5em;
}
</style>
