import {
  call as valee
} from "../lib/valee";
import api from "../lib/api";

export default {
  mounted() {
    this.read();
  },
  data() {
    return {
      list: [],
      form: {},
      formOriginal: {},
      errors: {},
      ui: {
        formVisible: true
      },
      total: 0,
      readParam: {
        limit: 7,
        page: 1,
      },
    }
  },
  methods: {
    flip(page) {
      this.readParam.page = page;
      this.read();
    },
    createOrUpdate() {
      let f = this.form;
      let action;

      if (!this.validateForm()) return;

      if (f.id) action = "update";
      else action = "create";

      api(`${this.model}/${action}`, f).then(r => {
        if (r.success) {
          this.read();
          this.resetForm();
        }
      });
    },
    resetForm() {
      this.ui.formVisible = false;
      this.form = {};
      this.errors = {};
    },
    validateForm() {
      let rules = this.rules;
      let f = this.form;
      let isUpdate = !!f.id;

      let valid = true;

      for (let field in rules) {
        // field nickname, phone, password

        // 
        if (isUpdate && f[field] === this.formOriginal[field]) continue;

        if (!this.validateField(field)) valid = false;
      }

      return valid;
    },

    validateField(field) {
      let value = this.form[field];
      let fieldValid = true;

      // field
      //  field nickname 
      // {unique: {...}, required: {...}, ...}
      let rules = this.rules[field];

      for (let key in rules) {
        // rule
        let rule = rules[key];
        let params = rule.params || []; // 

        let valid = valee(key, value, ...params);

        // errors
        if (typeof valid == "boolean") {
          this.afterValidate(field, key, valid);
          if (!valid) fieldValid = false;
        } else {
          // 
          valid.then(r => {
            this.afterValidate(field, key, r);
            // if (!r) fieldValid = false;
          });
        }
      }

      return fieldValid;
    },

    afterValidate(field, rule, valid) {
      // 
      let fieldObj = this.errors[field];

      // 
      if (!fieldObj) fieldObj = this.$set(this.errors, field, {});

      // valee
      // fieldObj['lengthBetween'] = true;
      this.$set(fieldObj, rule, !valid);
    },

    edit(index, row) {
      this.ui.formVisible = true;
      this.formOriginal = row;
      this.form = {
        ...row
      };
    },
    remove(index) {
      if (!confirm("")) return;
      api(`${this.model}/delete`, {
        id: index
      }).then(r => {
        if (r.success) {
          this.read();
        }
      });
    },
    read() {
      api(`${this.model}/read`, this.readParam).then(r => {
        this.list = r.data;
        this.total = r.total;
      });
    },
    makeSelect (prop) {
      return it => {
        this.form[ prop ] = it.id;
      };
    },
  },
};